module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-date-picker-time',\n  template: `\n    <nz-date-picker\n      nzShowTime\n      nzFormat=\"yyyy-MM-dd HH:mm:ss\"\n      nzPlaceHolder=\"Select Time\"\n      (ngModelChange)=\"onChange($event)\"\n      (nzOnOk)=\"onOk($event)\"\n    ></nz-date-picker>\n    <br>\n    <nz-range-picker\n      [nzShowTime]=\"{ nzFormat: 'HH:mm' }\"\n      nzFormat=\"yyyy-MM-dd HH:mm\"\n      [nzPlaceHolder]=\"[ 'Start Time', 'End Time' ]\"\n      (ngModelChange)=\"onChange($event)\"\n      (nzOnOk)=\"onOk($event)\"\n    ></nz-range-picker>\n  `,\n  styles  : [ `\n    nz-date-picker, nz-month-picker, nz-range-picker, nz-week-picker {\n      margin: 0 8px 12px 0;\n    }\n  ` ]\n})\n\nexport class NzDemoDatePickerTimeComponent {\n  onChange(result: Date): void {\n    console.log('Selected Time: ', result);\n  }\n\n  onOk(result: Date): void {\n    console.log('onOk', result);\n  }\n}\n"